import React from "react"
import { graphql } from "gatsby"
import styled from "styled-components"
import posed from "react-pose"
import Logo from "../components/logo"
import Img from "gatsby-image"


const Transition = posed.div({
  preEnter: {
    y: 1000,
    opacity: 1,
  },
  enter: {
    opacity: 1,
    y: 0,
    transition: {
      duration: 600,
      ease: 'easeInOut'
      }
  },
    exit: { 
      y: 0,
      opacity: 1,
    transition: {
      duration: 600,
      ease: 'easeInOut'
      },
      
  },
})

const Information = styled.div`
  background-color: ${props => props.color};
  font-family: 'Calibre-Regular';
  color: white;
  height: 100%;
  position: relative;
  z-index: 990;

  @media(min-width: 992px) {
    height: 100%;
  }
`
const Title = styled.h1`
  font-size: 48px;
  margin-left: 25px;
  margin-right: 25px;
  padding-top: 25px;
`

const Text = styled.p`
  font-size: 24px;
  margin-left: 25px;
  margin-right: 25px;
  padding-top: 25px;
  max-width: 800px;
  z-index: -1;
`
const InfiniteScroll = styled.div`
  height: 100vh;
  overflow-y: scroll;
`


const Page = ({ data }) => {

  // const { data } = prismicPage;

  console.log(data.allPrismicPage.edges);

  var itemsPre = function(prismicPage) {
    this.name = prismicPage.data.title.text;
    this.tags = [];
    prismicPage.tags.map(tag => {return this.tags.push(tag)});
  }

  var items = new itemsPre(data.prismicPage);

  console.log(items);
  console.log(data.image_gallery);


  return (
    <Transition>
      <InfiniteScroll>
      
      {data.prismicPage.data.image_gallery.map((image, index) => (
        <Img fluid={image.image.localFile.childImageSharp.fluid} />
          // <img key={index} src={image.image.url} />
      ))
      }

      {/* <Logo/> */}
      <Information color={`${data.prismicPage.data.color}`}>
        <Title>{items.name}</Title>
        <Text>{data.prismicPage.data.text.text}</Text>
      </Information>

      {
        data.allPrismicPage.edges.map((data) => {
          return (
            <div>
            {data.node.data.image_gallery.map((image, index) => (
              <Img fluid={image.image.localFile.childImageSharp.fluid} />
                // <img key={index} src={image.image.url} />
            ))
            }
                     
          <Information color={`${data.node.data.color}`}>
            <Title>{data.node.data.title.text}</Title>
            <Text>{data.node.data.text.text}</Text>
          </Information>

          </div>
            )
        })
      }
      </InfiniteScroll> 
    </Transition>  
  )
}

export default Page

export const pageQuery = graphql`
  query PostBySlug($uid: String!) {
    prismicPage(uid: { eq: $uid }) {
      tags
      uid
      data {
        title {
          text
        }
        text {
          text
        }
        color
        image_gallery {
          image {
            alt
            copyright
            url
            localFile {
              childImageSharp {
                fluid(maxWidth: 1500) {
                  ...GatsbyImageSharpFluid_withWebp
                }
              }
            }
          }
        }
      }
    },
    allPrismicPage(filter: { uid: {ne: $uid}}) {
      edges {
        node {
          id
          uid
          data {
            title {
              text
            }
            text {
              text
            }
            color
            image_gallery {
              image {
                alt
                url
                localFile {
                  childImageSharp {
                    fluid(maxWidth: 1500) {
                      ...GatsbyImageSharpFluid_withWebp
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
`
